.header {
    min-height: 658px;
    background: conic-gradient(from 139.4deg at 20.7% 35.74%, #B80027 -46.69deg, #003D85 32.24deg, #B80027 313.31deg, #003D85 392.24deg);
    padding-top: 30px;

    @include vp-1199 {
        min-height: 554px;
        padding-top: 34px;
        background: conic-gradient(from 139.4deg at 20.7% 35.74%, #B80027 -46.69deg, #003D85 32.24deg, #B80027 313.31deg, #003D85 392.24deg);
    }

    @include vp-767 {
        min-height: 492px;
        padding-top: 15px;
        background: conic-gradient(from 171.43deg at 35.31% 34.22%, #B80027 -46.69deg, #003D85 32.24deg, #B80027 313.31deg, #003D85 392.24deg);
    }
}

.header__nav {

    @include vp-1199 {
        margin-bottom: 18px;
    }

    @include vp-767 {
        margin-bottom: 12px;
    }
}

.header__nav .container {
    display: flex;
    justify-content: space-between;
}

.header__logo {

    @include vp-1199 {
        padding-top: 6px;
    }

    @include vp-767 {
        padding-top: 0;
    }
}

.header__logo-img {

    @include vp-1199 {
        width: 81px;
        height: 16px;
    }

    @include vp-767 {
        width: 69px;
        height: 24px;
    }
}

.header__tel {

    @include font-settings ($ff-alumni, 600, 26px, 1.19, $color-default-white);

    letter-spacing: 0.03em;
    padding-top: 1px;

    @include vp-1199 {
        font-weight: 700;
        font-size: 24px;
        line-height: 1.2;
        letter-spacing: 0.02em;
    }

    @include vp-767 {
        font-weight: 600;
        font-size: 20px;
    }
}
