}

.abonements h2 {
  margin: 0 0 12px;
  padding-right: 5px;
  text-align: center;
  letter-spacing: 0.03em;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 54px;
  line-height: 1.2;
  color: #102654;
}

.tabs {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column;
}

.tabs__list {
  position: relative;
  display: flex;
  justify-content: space-between;
  min-width: 387px;
  margin: 0 0 46px;
  padding: 0;
  list-style: none;
}
.tabs__list::after {
  position: absolute;
  content: "";
  bottom: -8px;
  left: 25px;
  width: calc(100% - 51px);
  height: 2px;
  background-color: rgba(120, 120, 120, 0.2);
}

.tab__item {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.5;
  color: rgba(16, 38, 84, 0.5);
  position: relative;
  cursor: pointer;
  padding: 5px 24px;
}

.tab__item.is-active {
  color: #102654;
}
.tab__item.is-active::after {
  position: absolute;
  content: "";
  bottom: -8px;
  left: 25px;
  width: calc(100% - 48px);
  height: 2px;
  background-color: #d10a42;
}

.tabs__contents {
  width: 100%;
  height: 100%;
}

.tabs__content {
  display: none;
}

.tabs__content.is-active {
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 1199px) {
  .tabs__content.is-active {
    flex-direction: column;
  }
}

.tabs__content-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 360px;
  min-height: 448px;
  padding-top: 56px;
  background-color: #ffffff;
  z-index: 1;
  border: 7px solid #ffffff;
}
.tabs__content-item:hover, .tabs__content-item:focus-visible {
  border: 10px solid;
  border-image-slice: 1;
  border-width: 7px;
  border-image-source: linear-gradient(148.82deg, #D10A42 0.85%, #00359F 100%);
}

.tabs__content-item h3 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 26px;
  line-height: 1.12;
  color: #102654;
  margin: 0 0 10px;
}

.tabs__content-item p {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  color: #495a7d;
  margin: 0 0 65px;
}

.tabs__content-item span {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 60px;
  line-height: 1.5;
  color: #103aae;
  position: relative;
  padding-right: 45px;
  margin: 0 0 34px;
}
.tabs__content-item span::before {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 100px;
  line-height: 1.5;
  color: rgba(232, 232, 232, 0.48);
  position: absolute;
  content: attr(title);
  top: -54px;
  right: -16px;
  z-index: -1;
}
.tabs__content-item span::after {
  position: absolute;
  content: url("../../img/svg/rouble.svg");
  top: 0;
  right: 0;
}

.tabs__btn {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  line-height: 1.16;
  color: #102654;
  width: 208px;
  height: 66px;
  padding-top: 3px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}

.tabs__content-item:hover > .tabs__btn,
.tabs__btn:focus-visible {
  color: #ffffff;
  background: linear-gradient(97.05deg, #ED0233 17.6%, #003EB7 95.31%);
  border: none;
  outline: none;
}

